<template>
 <div class="all">
  <!-- 水下摄像头部分 -->
  <div class="video">
     
     <img src="http://121.43.229.134:8080/video_feed" alt="加载失败" title="水下摄像头">
  </div>
  <!-- 地图部分 -->
  <div class="map">
    <!-- 输入框 -->
    <!-- <div>
    <el-input placeholder="请输入纬度" v-model="input1">
    <template slot="prepend">纬度</template>
    </el-input>
    <el-input placeholder="请输入经度" v-model="input2">
    <template slot="prepend">经度</template>
    </el-input>
    </div> -->
    <!-- 四个按钮 -->
    <!-- <div class="button">
       <el-button round type="primary" size="medium">自我定位</el-button>
       <el-button round type="primary" size="medium">规划路径</el-button>
       <el-button round type="primary" size="medium">--开始--</el-button>
       <el-button round type="primary" size="medium">--停止--</el-button>
    </div> -->
    <!-- 位置 -->
    <span>高德地图</span>
    <div class="Map" id="ditu">
    </div>
  </div>
 </div>
</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader';

// 设置安全密钥，这里使用的是示例值，请替换为你自己的安全密钥
window._AMapSecurityConfig = {
  securityJsCode: 'f789e6b6d9300227e0d5872d17b286ef'
};

export default {
  name: 'MapComponent',
  data() {
    return {
      // input1:'',
      // input2:'',
      map: null, // 地图实例
      markers: [ // 标记点数组
        { name: '监测点1', position: [113.02901, 28.24660] },
        { name: '监测点2', position: [113.02876, 28.24660] },
        { name: '监测点3', position: [113.02887, 28.24456] },
        { name: '监测点4', position: [113.02927, 28.24456] },
        { name: '船体位置', position: [113.02911, 28.24530] },
      ],
    };
  },
  mounted() {
    this.initMap();
  },
  methods: {
    // 获取当前位置
    getCurrentLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const coords = position.coords;
            const location = [coords.longitude, coords.latitude];
            // 定位成功后设置地图中心点
            this.map.setCenter(location);
            // 可以在这里添加标记或其他操作
          },
          () => {
            console.error('定位失败，请检查浏览器定位权限设置');
          }
        );
      } else {
        console.error('浏览器不支持地理定位');
      }
    },
    // 初始化地图
    initMap() {
      AMapLoader.load({
        key: "77ba5144f8a53988f957c53d9dd7f0ca", // 替换为你的开发者Key
        version: "2.0",
        plugins: ['AMap.Geolocation', 'AMap.Marker'], // 添加定位插件和标记点插件
      }).then((AMap) => {
        this.map = new AMap.Map("ditu", {
          viewMode: "3D",
          zoom: 18,
          center: [113.02911, 28.24530], // 经纬度坐标
        });

        // 地图添加定位插件
        this.map.addControl(new AMap.Geolocation({
          provider: 'browser', // 使用浏览器定位
          zoomToAccuracy: true, // 定位成功后是否自动调整地图视野到定位点
        }));

        // 调用定位方法
        this.getCurrentLocation();

        // 添加标记点
        this.addMarkers();
      }).catch(e => {
        console.log(e);
      });
    },
    // 添加标记点
addMarkers() {
  this.markers.forEach((marker) => {
    const markerInstance = new AMap.Marker({
      position: marker.position,
      map: this.map,
      // 使用 label 属性来添加一个文本标签
      label: {
        offset: new AMap.Pixel(0, 0), // 设置文本标签的偏移量
        content: marker.name, // 设置文本标签的内容，即标记点的名字
        direction: 'top', // 设置文本标签的方向
      }
    });
  });
},
  },
};
</script>

