<template>
  <div>
    <div class="all">
      <!-- 摄像头部分 -->
      <div class="video">
        <!-- <img src="http://121.43.229.134:8080/video_feed" alt="加载失败" title="水上摄像头"> -->
      </div>
      <!-- 开关部分 -->
      <div class="photo">
        <div class="same">
          <i class="el-icon-first-aid-kit"></i>水泵
          <el-switch
           v-model="pump"
           active-color="#2bd6f0"
           inactive-color="#e9eef3"
           id="pump"
           @change="changeSwitch('pump')">
          </el-switch>
        </div>
        <div class="same">
          <i class="el-icon-table-lamp"></i>灯
          <el-switch
           v-model="light"
           active-color="#2bd6f0"
           inactive-color="#e9eef3"
           id="light"
           @change="changeSwitch('light')">
          </el-switch>
        </div>
        <!-- <div class="same">
          <i class="el-icon-odometer"></i>氧气泵
          <el-switch
           v-model="oxygen_pump"
           active-color="#2bd6f0"
           inactive-color="#e9eef3"
           id="oxygen_pump"
           @change="changeSwitch('oxygen_pump')">
          </el-switch>
        </div> -->
        <div class="same">
          <i class="el-icon-view"></i>摄像头
          <el-switch
           v-model="video"
           active-color="#2bd6f0"
           inactive-color="#e9eef3"
           id="video"
           @change="changeSwitch('video')">
          </el-switch>
        </div>
        <div class="same">
           <i class="el-icon-orange"></i>水质取样
          <el-switch
           v-model="waterdata"
           active-color="#2bd6f0"
           inactive-color="#e9eef3"
           id="waterdata"
           @change="changeSwitch('waterdata')">
          </el-switch>
      </div>
    </div> 
  </div>
    <!-- 按钮控制部分 -->
    <div class="control">
      <div class="a">
        <!-- //font-size设置图标大小 -->
        <div class="text">船体控制</div>
        <el-button :data-direction="'up'" :data-target="'boat'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif1=true" icon="el-icon-arrow-up"  style="font-size: 50px;margin-bottom: 10px;" circle></el-button>
        <div>
        <el-button :data-direction="'left'" :data-target="'boat'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif1=true" icon="el-icon-arrow-left"  style="font-size: 50px;" circle></el-button>
        <el-button :data-direction="'stop'" :data-target="'boat'" @touchstart.native="clickBtn"  @click="iconif1=!iconif1" :icon="iconif1?'el-icon-video-pause':'el-icon-video-play'"  style="font-size: 50px;" circle></el-button>
        <el-button :data-direction="'right'" :data-target="'boat'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif1=true" icon="el-icon-arrow-right"  style="font-size: 50px;" circle></el-button>
        </div>
        <el-button :data-direction="'down'" :data-target="'boat'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif1=true" icon="el-icon-arrow-down"  style="font-size: 50px;margin-top: 10px;" circle></el-button>
      </div>
      <div class="b">
        <!-- //font-size设置图标大小 -->
        <div class="text">舵机控制</div>
        <el-button :data-direction="'up'" :data-target="'engine'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif2=true" icon="el-icon-arrow-up"  style="font-size: 50px;margin-bottom: 10px;" circle></el-button>
        <div>
        <el-button :data-direction="'left'" :data-target="'engine'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif2=true" icon="el-icon-arrow-left"  style="font-size: 50px;" circle></el-button>
        <el-button :data-direction="'stop'" :data-target="'engine'" @touchstart.native="clickBtn"  @click="iconif2=!iconif2" :icon="iconif2?'el-icon-video-pause':'el-icon-video-play'"  style="font-size: 50px;" circle></el-button>
        <el-button :data-direction="'right'" :data-target="'engine'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif2=true" icon="el-icon-arrow-right"  style="font-size: 50px;" circle></el-button>
        </div>
        <el-button :data-direction="'down'" :data-target="'engine'" @touchstart.native="clickBtn" @touchend.native="releaseMouse" @click="iconif2=true" icon="el-icon-arrow-down"  style="font-size: 50px;margin-top: 10px;" circle></el-button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name:"seven",
  data() {
    return {
      iconif1:true,
      iconif2:true,
      pump: false,
      light:false,
      oxygen_pump: false,
      video: true,
      waterdata:false
    };
  },
  methods: {
    clickBtn(event){
      const direction = event.currentTarget.dataset.direction;
      const target = event.currentTarget.dataset.target;
      console.log(`${direction} ${target} button is clicked`);
      this.control(direction, target, 'on');
    },
    releaseMouse(event){
      const direction = event.currentTarget.dataset.direction;
      const target = event.currentTarget.dataset.target;
      console.log(`${direction} ${target} button is released`);
      this.controlStop(direction, target, 'stop');
    },
    control(direction, target, action) {
      //船体控制
      if(target==='boat'){
        //向上
        if(direction==='up'){
          axios.get(this.$buildUrl('/control/motor_forward_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向下
        if(direction==='down'){
          axios.get(this.$buildUrl('/control/motor_backward_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向左
         if(direction==='left'){
          axios.get(this.$buildUrl('/control/motor_left_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向右
         if(direction==='right'){
          axios.get(this.$buildUrl('/control/motor_right_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //停止
         if(direction==='stop'){
          axios.get(this.$buildUrl('/control/motor_stop' ))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
      }
      //舵机控制
      else if(target==='engine'){
         if(direction==='up'){
          axios.get(this.$buildUrl('/control/servo_forward_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向下
        if(direction==='down'){
          axios.get(this.$buildUrl('/control/servo_backward_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向左
         if(direction==='left'){
          axios.get(this.$buildUrl('/control/servo_left_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向右
         if(direction==='right'){
          axios.get(this.$buildUrl('/control/servo_right_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //停止
         if(direction==='stop'){
          axios.get(this.$buildUrl('/control/servo_stop'))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
      }
    },
    controlStop(direction, target, action) {
      if(target==='boat'){
        //向上
        if(direction==='up'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向下
        if(direction==='down'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向左
         if(direction==='left'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向右
         if(direction==='right'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
      }
      //舵机控制
      else if(target==='engine'){
         if(direction==='up'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向下
        if(direction==='down'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向左
         if(direction==='left'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
        //向右
         if(direction==='right'){
          axios.get(this.$buildUrl('/control/motor_'+action))
          .then(response=>{
            // console.log(response.data);
          })
          .catch(error=>{
            // console.error(error);
          })
        }
      }
    },
    changeSwitch(switchId) {
      
      let target= switchId;
      let checked =this[switchId];
      // console.log(target);
      // console.log(checked);
      let url='';
      switch (target) {
        case 'pump':
          url = checked ? this.$buildUrl('/control/pump_on'): this.$buildUrl('/control/pump_off');
          break;
        case 'light':
          url = checked ? this.$buildUrl('/control/light_on') : this.$buildUrl('/control/light_off');
          break;
        case 'oxygen_pump':
          url = checked ? this.$buildUrl('/control/oxygen_pump_on') : this.$buildUrl('/control/oxygen_pump_off');
          break;
        // case 'feeding':
        //   url = checked ? this.$buildUrl('/control/feeding_on') : this.$buildUrl('/control/feeding_off'); // 注意这里修正了URL
        //   break;
        case 'video':
          url = checked ? this.$buildUrl('/video_start') : this.$buildUrl('/video_stop');
          break;
        default:
          break;
      }

      axios.get(url).then(() => {
         let data = {};
         data[target] = checked;
      }).catch(error => {
        // 处理错误
        console.error(error);
      });

      // 第二个请求，发送POST数据
      axios.post(this.$buildUrl('/btnState/changeBtn'), {
        target: target,
        checked: checked,
      })
      .then(response => {
        // 请求成功
        // console.log(response.data);
      })
      .catch(error => {
        // 请求失败
        console.log(error);
      });
    },
    transform(value){
      if(value==1)
        return true
    },
    getBtnState() {
    axios.get(this.$buildUrl('/btnState/getBtnState'))
      .then((res) => {
          console.log(res.data.data);
          this.pump = this.transform(res.data.data.pump);
          this.light = this.transform(res.data.data.light);
          this.oxygen_pump =this.transform(res.data.data.oxygen_pump);
          // this.feeding =this.transform(res.data.data.feeding);
          this.video = this.transform(res.data.data.video);
      })
      .catch((error) => {
        console.error(error);
      });
  },
  },
  
created(){
  this.getBtnState();
}
};
</script>

<style scoped>
.all{
   float: left;
   height:725px;
   width:60%;
   border:5px solid #2bd6f0;
   border-radius: 2%;
}

.video{
  height:75%;
  width:100%;
  border-bottom:5px solid #2bd6f0;
}

img{
  height: 100%;
  width: 100%;
}

.control{
  float: left;
  height: 725px;
  width: 40%;
}

.text{
  font-size: 23px;
  margin-top: 5px;
  margin-bottom: 25px;
}
.a,.b{
  height: 50%;
  width: 95%;
  background-color: #E9EEF3;
  border-radius: 50%;
  margin: auto;
  border:1px solid red;
}

.photo{
  height: 25%;
  width: 100%;
} 

 .same{
   width:100%;
   height: 17%;
   font-size: 26px;
   text-align: left;
   margin-left: 10px;
}

i{
  margin-right: 10px;
}

 

.switch-container{
  float: left;
  margin-left: 10px;
}

</style>

