var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"A"},[_c('div',{staticClass:"a"},[_c('h3',[_vm._v("一、国内河道排污监测无人船发展现状")]),_c('span',[_vm._v("        自从环保工作的重要性被大众认知，相关部门在环保工作上的投入力度越来越大，随着2016年12月11日，《关于全面推行河长制的意见》正式出台，以及2017年9月1日环保部对于“全国地表水环境质量监测事权上收”的任务下达，作为“排头兵”的环境监测领域也迎来了它的新成员——无人船。无人船学名叫“水面机器人”，是一种不需要人来操控，可以完全独立自主执行任务的船只，比较适合执行危险或者枯燥、重复的军用、民用任务，例如运用在军事打击、安防巡逻、地貌测绘、环境监测等领域。 无人监测船实现了无人船和水质监测的“跨界融合”，基于这种移动在线监测，可以实现很多过去实现不了的功能，比如把一个湖面地毯式监测一遍之后就可以拿到水质参数分布图，对于污染的分布、扩散情况一目了然，特别是对现在每年我国各大湖水藻爆发的预警和防治提出了新的办法和思路。 另外，利用无人监测船传回的即时信息，可以随时跟踪污染扩散的状况，增加了信息的透明度。采用无人船平台，可以搭载常规的单波束声呐、多波束声呐、侧扫声呐、浅层地表剖析仪器，并可配备常用小目标识别雷达、AIS、气象站、360度全视角云台、夜视摄像头等，为执行不同的任务提供保障。 ")])]),_c('div',{staticClass:"b"},[_c('h3',[_vm._v("二、国外河道排污监测无人船发展现状")]),_c('span',[_vm._v("        全球无人船市场规模在过去几年中持续攀升，据预测，到2025年，全球无人船市场规模将达到160亿美元，其中，海洋无人船占比将达到60%以上；到2030年，全球无人船市场规模有望达到400亿美元。 无人船巨大的发展潜力吸引了众多国家和企业纷纷投入研发和生产，竞相研发具有自主知识产权的无人船技术。目前，美国、英国、挪威、荷兰等国家在无人船技术领域取得了显著成果，形成了较为完善的产业链和商业模式。 国外排污检测无人船的发展现状呈现出积极的态势。随着技术的不断进步、政策的持续支持以及市场需求的增加，无人船市场规模正在不断扩大，具有广阔的市场前景和发展潜力。在排污检测领域，无人船的应用日益广泛，其可以自主执行排污检测任务，提高检测效率和准确性，同时降低人员风险和成本。 具体来说，一些先进的无人船已经配备了高精度传感器和智能分析系统，能够实时监测水质变化、识别污染源，并通过数据分析提供决策支持。此外，无人船还可以与其他环保设备进行联动，实现更高效的排污治理。 然而，需要注意的是，无人船在排污检测领域的应用仍处于发展阶段，还需要在技术、政策和市场等方面不断完善和提升。例如，需要加强无人船在复杂环境下的稳定性和可靠性，提高其自主决策和应对突发情况的能力；同时，还需要制定和完善相关的法规和标准，规范无人船在排污检测领域的应用和管理。 ")])]),_c('div',{staticClass:"c"},[_c('h3',[_vm._v("三、发展动态")]),_c('span',[_vm._v("        随着近年来国家对环保的重视以及水面保洁问题的日益放大，水面智能化保洁装备也开始走入人们的视野，将无人船技术应用于常规的水面保洁将是未来的趋势。在无人船这个领域，国内已经有不少公司已经在做相关业务，同时也积攒了一定的技术经验。而且无人船正在从军用到民用逐步渗透。国内各无人船企业纷纷推出不同种类的无人船，例如：环境监测无人船、海洋调查无人船、无人安防救援船、无人航运、无人驾驶清洁船、无人驾驶割草船等。水质采样普遍使用无人船代替传统的人工作业，无人船水质采样不仅作业效率高而且还方便搬运；除此之外，国内现在的河道普查，也已经在使用无人船搭载侧扫或者多波束声纳来作业，在经济层面上节省了人工；以及河道巡逻安防、河面保洁等都可以应用无人船技术。 在当下，精准治污、科学治污已经成为常态。各地各级的联合执法队伍形成了河湖管理保护的强大合力。在河湖环境的实际巡查工作中，快速大面积获取区域信息、重点区域精准取证，以及巡查高效性、取证手段灵活性上有更高的要求。针对这类痛点，无人船可以很好的发挥功用。 ")])])])
}]

