import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/views/login'
import Layout from '@/views/layout'
import One from '@/views/layout/1'
import three from '@/views/layout/3'
import four from '@/views/layout/4'
import five from '@/views/layout/5'
import seven from '@/views/layout/7'


//二级路由







Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    //登入页面
    { path: '/', component: Login },
    //主页面
    {
      path: '/layout',
      component: Layout,
      redirect: '/layout/7',
      children:
        [
          { path: '7', component: seven },
          { path: '1', component: One },
          { path: '3', component: three },
          { path: '4', component: four },
          { path: '5', component: five },

        ]
    },
  ]
})

export default router
