<template>
  <div class="all">
    <!-- 数据异常情况 -->
     <div class="a">
        <div class="container">
          <span class="title">温度异常记录</span>
          <div class="data-item" v-for="(item, index) in temperatureAbnormalData" :key="index" >
           <span class="time">{{ item.time }}</span>
           <span class="value" style="color: rgb(2, 156, 2);">{{ item.value }}℃ </span>
           <span class="message">{{ item.message }}</span>
          </div>
        </div>

        <div class="container">
          <span class="title">含氧量异常记录</span>
          <div class="data-item" v-for="(item, index) in oxygenAbnormalData" :key="index">
           <span class="time">{{ item.time }}</span>
           <span class="value" style="color: rgb(16, 103, 235);">{{ item.value }}% </span>
           <span class="message">{{ item.message }}</span>
          </div>
        </div>

        <div class="container">
          <span class="title">浊度异常记录</span>
          <div class="data-item" v-for="(item, index) in turbidityAbnormalData" :key="index">
           <span class="time">{{ item.time }}</span>
           <span class="value" style="color: red;">{{ item.value }} NTU </span>
           <span class="message">{{ item.message }}</span>
          </div>
        </div>

        <div class="container">
          <span class="title">pH异常记录</span>
          <div class="data-item" v-for="(item, index) in pHAbnormalData" :key="index" >
           <span class="time">{{ item.time }}</span>
           <span class="value" style="color: purple;">{{ item.value }} Mol·L-1 </span>
           <span class="message">{{ item.message }}</span>
          </div>
        </div>
     </div>

     <!-- 报警情况 -->
      <div class="b">
            <div class="img-item"> 
              <div v-for="(url, index) in imgUrlList" :key="index">
                <el-image 
                 :src="url" 
                 :preview-src-list="imgUrlList">
                </el-image>
              </div>
            </div>
          <button @click="downloadImg"> 
            <span class="text1">点击下载图片</span>
            <span>(点击图片可浏览)</span>
          </button>
         
      </div>
</div>
      
</template>

<script>
import axios from 'axios';
export default {
  data(){
    return{
      abnormalData: [],
      temperatureAbnormalData: [],
      oxygenAbnormalData: [],
      turbidityAbnormalData: [],
      pHAbnormalData: [],
      time:'2024-10-14 18:16:12',
      value:'46',
      message:'正常',
      imgUrlList: []
    }
  },
  created() {
    this.fetchData();
  },
  methods:{
    downloadImg() {
      const imgUrlList = [
         require("../../../assets/1.jpg"), // 使用require来引用本地图片
         require("../../../assets/2.jpg"), 
         require("../../../assets/3.jpg"),
      ];
      this.imgUrlList = imgUrlList;
    },
     fetchData() {
      axios.get(this.$buildUrl('/abnormalData'))
        .then((response) => {
          const data = response.data;
          this.abnormalData = data;
          this.temperatureAbnormalData = data.filter(item => item.type === "温度");
          this.oxygenAbnormalData = data.filter(item => item.type === "含氧量");
          this.turbidityAbnormalData = data.filter(item => item.type === "浊度");
          this.pHAbnormalData = data.filter(item => item.type === "pH");
        })
        .catch((error) => {
          console.error('请求异常记录数据失败', error);
        });
        //添加测试数据
        const testData1 = {
        time: "2022-01-04 09:15",
        type: "温度",
        value: 46,
        message: "温度异常"
      };
      const testData2 = {
        time: "2022-01-05 12:30",
        type: "含氧量",
        value: 90,
        message: "含氧异常"
      };
      const testData3 = {
        time: "2022-01-05 12:30",
        type: "浊度",
        value: 90,
        message: "浊度异常"
      };
      const testData4 = {
        time: "2022-01-05 12:30",
        type: "浊度",
        value: 34,
        message: "浊度异常"
      };
      const testData5 = {
        time: "2022-01-05 12:30",
        type: "pH",
        value: 10,
        message: "pH异常"
      };
      const testData = [testData1, testData2, testData3, testData4, testData5];
      this.abnormalData = [...this.abnormalData, ...testData];
      this.temperatureAbnormalData = [...this.temperatureAbnormalData, ...testData.filter(item => item.type === "温度")];
      this.oxygenAbnormalData = [...this.oxygenAbnormalData, ...testData.filter(item => item.type === "含氧量")];
      this.turbidityAbnormalData = [...this.turbidityAbnormalData, ...testData.filter(item => item.type === "浊度")];
      this.pHAbnormalData = [...this.pHAbnormalData, ...testData.filter(item => item.type === "pH")];
    },
  }
}
</script>

<style scoped>
.all{
  height:100%;
  width:100%;
}

.a{
  float: left;
  width: 50%;
  height: 100%;
  background-color: #bbe1fc;
  border-right:2px dashed black;
}

.b{
  position: relative;
  float: left;
  width: 50%;
  height: 100%;
  background-color:  #bbe1fc;
}

.container {
  margin: 25px;
  padding: 15px;
  background-color: #ffffff;
}

.title {
  display: block;
  font-size: 23px;
  font-weight: 700;
  margin-bottom: 25px;
}


.data-item {
  display: flex;
  align-items: center;
	padding: 10px;
	border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}

.time{
	flex: 3;
	display: flex;
	font-size: 17px;
  justify-content: center;
  align-items: center;
}

.value{
	flex: 1;
	display: flex;
	font-size: 17px;
  justify-content: center;
  align-items: center;
}

.message {
	flex: 2;
	display: flex;
	font-size: 17px;
  justify-content: center;
  align-items: center;
}

  button {
  position: absolute;
  top: 580px;
  right: 235px;
  padding: 10px;
  justify-content: flex-end; 
  background: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
  border-radius: 6%;
}

.text1{
  display: flex;
  justify-content: center;
  color: black;
  font-family: "微软雅黑";  
  font-weight: bold;  
  font-size: 25px;
  cursor: pointer;
}

.img-item {
  display: flex;
  justify-content: space-around; /* 子元素在主轴上的对齐方式 */
  align-items: center; /* 子元素在交叉轴上的对齐方式 */
  flex-wrap: wrap; /* 自动换行 */
  margin: 20px;
}

.el-image{
  width: 150px;
  height: 125px;
  border-radius: 15%;
  cursor: pointer;
}

</style>