      &nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      我国近十多年一直在发展在线监测技术，在水面放置浮漂，或者在湖边安装设备，通过管道抽取水样。但这种技术的建设成本比较高，而且监测位置是固定的，在天气不好，或者湖比较小的情况下，卫星遥感监测就无法进行。为了保护水资源，维护水环境，我国正在积极采取措施进行水环境监测管理，更多智能化的新型监测系统——河道排污监测系统也随之出现，让排污管理工作更具现代化，具有重要的现实意义。本项目设计实现以下特色：
    </span>
    <div class="a">
