<template>
  <div id="a" style="height: 673px;background-color: #fff;margin:10px;"></div>
</template>

<script>
import * as echarts from 'echarts';
import axios from 'axios';

export default { 
data(){
  return{
    myChart:null,
    option :{
  title: {
    text: '实时监测数据'
  },
  tooltip: {
    trigger: 'axis'
  },
  legend: {
    data: ['浑浊度', 'pH', '温度', '含氧量']
  },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  toolbox: {
    feature: {
      saveAsImage: {}
    }
  },
  xAxis: {
    type: 'category',
    boundaryGap: false,
    data: []
  },
  yAxis: {
    type: 'value'
  },
  series: [
    {
      name: '浑浊度',
      type: 'line',
      stack: 'Total',
      data: []
    },
    {
      name: 'pH',
      type: 'line',
      stack: 'Total',
      data: []
    },
    {
      name: '温度',
      type: 'line',
      stack: 'Total',
      data: []
    },
    {
      name: '含氧量',
      type: 'line',
      stack: 'Total',
      data: []
    }
  ]
   }
  };
  },
 methods: {
    fetchData() {
      Promise.all([
        axios.get(this.$buildUrl('/control/setData')),
        axios.get(this.$buildUrl('/control/setData1')),
        axios.get(this.$buildUrl('/control/setData2')),
        axios.get(this.$buildUrl('/control/setData3'))
      ]).then(responses => {
        const [res1, res2, res3, res4] = responses;
        this.option.xAxis.data = res3.data.time.reverse(); // 时间数据
        this.option.series[0].data = res1.data.data.reverse(); // 浊度数据
        this.option.series[1].data = res2.data.data.reverse(); // 温度数据
        this.option.series[2].data = res3.data.data; // 含氧量数据
        this.option.series[3].data = res4.data.data; // pH 数据
        this.myChart.setOption(this.option);
      }).catch(error => {
        console.error('请求数据失败:', error);
      });
    }
  },

mounted(){
    this.myChart = echarts.init(document.getElementById('a'));
    this.myChart.setOption(this.option);
    this.fetchData();
},

};


</script>

