<template>
<el-container>
  <el-aside width="200px">
    <!-- 主导航栏（页面右侧） -->
    <div class="boot">无人船河道排污监测系统</div>
    <asideNav></asideNav>
    <!-- 导航end -->
  </el-aside>
  <el-container>
    <el-header>
      <!-- 头部 -->
    <span class="time">北京时间: {{newTime}}</span>
    <div class="user-name">{{ msg }}</div>
    <div class="user-picture"></div>
    <!-- end -->
    </el-header>
    <el-main>
        <router-view></router-view>
    </el-main>
  </el-container>
</el-container>
</template>

<script>
import asideNav from '@/views/layout/asideNav.vue'
 
export default {
 name: 'Layout',
 data(){
  return {
      newTime: '',//时间
      msg:'',
  }
 },
 components:{
  asideNav
 },
 mounted(){
        this.getNowTime();//进入页面调用该方法获取当前时间
        clearInterval(myTimeDisplay );//销毁之前定时器
        var myTimeDisplay = setInterval(() => {
           this.getNowTime(); //每秒更新一次时间
        }, 1000); 
        //
        this.msg =this.$store.state.msg;
},
methods: {
        getNowTime() {
            var date = new Date();
            var time = date.getFullYear() + '-' + this.addZero((date.getMonth() + 1)) + '-' + this.addZero(date.getDate())+ ' ' + this.addZero(date.getHours()) + ':' + this.addZero(date.getMinutes()) + ':' + this.addZero(date.getSeconds());
            this.newTime = time; 
        },
        //补零
        addZero(s) {
            return s < 10 ? ('0' + s) : s;
        },
    },


 
}
</script>

<style scoped>
.el-header{
    background-color: #545c64;
    color: #333;
    text-align: center;
    line-height: 60px;
    padding: 0;
  }
  
  .el-aside {
    height: 100vh; 
    background-color: #545c64;
    color: #333;
    text-align: center;
    /* line-height: 200px; */
  }
  
  .el-main {
    background-color: #fff;
    color: #333;
    text-align: center;
    padding: 0;
  }
  .boot{
    height: 60px;
    width: 200px;
    line-height: 60px;
    text-align: center;
    background-color: #545c64 ;
    color: #fff;
  }


  .user-picture{
    float: right;
    height: 45px;
    width: 45px;
    background: url("../../assets/user.png");
    background-size:100% 100%;
    border-radius: 50%;
    margin:10px;
  }

  .user-name{
    float: right;
    margin-right: 30px;
    color: #fff;
  }

  .time{
    color: #fff;
    float: left;
    margin-left: 400px;
  }
</style>