<template>
 <div class="body">
  <div class="container">
    <div class="heading">Sign In</div>
    <form action="" class="form">
      <!-- required=""必填 -->
      <input v-model="usename"  class="input" type="test"   placeholder="usename">
      <input v-model="password" class="input" type="password" placeholder="Password">
      <el-button  @click="login" class="login-button" type="submit">Sign In</el-button>
    </form>
      <span class="agreement">无人船河道排污监测系统</span>
    </div>
  </div>
</template>

<script>
// 导入仓库

export default {
  name:'Login',
  data(){
    return {
      usename:'',
      password:'',
    }
  },
  methods:{
    login(){ 
         this.$store.commit("set_msg",this.usename);
        //end
         if(!/^\d{6}$/.test(this.password)||!/\D*[a-zA-Z]\D*/.test(this.usename)){
          this.$notify.error({
          title: '登入失败',
          message: '请输入正确的用户名称和密码',
          showClose: false,
          duration:850,
          })
         }
         else{
          //成功弹框
          this.$notify({
          title: '登入成功',
          message: '',
          type: 'success',
          showClose: false,
          duration:1000,
          });
          //
         this.$router.push('/layout')
         }
       }
    },
  }

</script>

<style lang="less" scoped>
   .body{
  background-color:  #409EFF;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  /* background: url("") no-repeat center center fixed; */
  background-size: cover;
}

.container {
  max-width: 500px;
  background: #F8F9FD;
  background: linear-gradient(0deg, rgb(255, 255, 255) 0%, rgb(244, 247, 251) 100%);
  border-radius: 40px;
  padding: 25px 35px;
  border: 5px solid rgb(255, 255, 255);
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 30px 30px -20px;
  margin: 20px;
}

.heading {
  text-align: center;
  font-weight: 900;
  font-size: 30px;
  color: rgb(16, 137, 211);
}

.form {
  margin-top: 20px;
}

.form .input {
  width: 100%;
  background: white;
  border: none;
  padding: 15px 5px;
  border-radius: 20px;
  margin-top: 15px;
  box-shadow: #cff0ff 0px 10px 10px -5px;
  border-inline: 2px solid transparent;
}

.form .input::-moz-placeholder {
  color: rgb(170, 170, 170);
}

.form .input::placeholder {
  color: rgb(170, 170, 170);
}

.form .input:focus {
  outline: none;
  border-inline: 2px solid #12B1D1;
}

.form .login-button {
  display: block;
  width: 100%;
  font-weight: bold;
  background: linear-gradient(45deg, rgb(16, 137, 211) 0%, rgb(18, 177, 209) 100%);
  color: white;
  padding-block: 15px;
  margin: 20px auto;
  border-radius: 20px;
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 20px 10px -15px;
  border: none;
  transition: all 0.2s ease-in-out;
}

.form .login-button:hover {
  transform: scale(1.03);
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 23px 10px -20px;
}

.form .login-button:active {
  transform: scale(0.95);
  box-shadow: rgba(133, 189, 215, 0.8784313725) 0px 15px 10px -10px;
}

.agreement {
  display: block;
  text-align: center;
  margin-top: 15px;
  text-decoration: none;
  color: #0099ff;
  font-size: 9px;
}
</style>